/**
 * Interface containing known page config properties
 */
export interface IPageConfig {
    /**
     * Page theme
     */
    pageTheme?: string;

    /**
     * Additional class names
     */
    className?: string;

    /**
     * Allow for accessing unknown properties but force callee to type the property
     */
    [propertyName: string]: unknown;
}
